"use client"

import { useState, useEffect } from "react"
import { useRouter, useParams } from "next/navigation"
import { Sidebar } from "@/components/layout/sidebar"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card"
import { Label } from "@/components/ui/label"
import { Input } from "@/components/ui/input"
import { ChevronLeft, Edit, Trash2 } from "lucide-react"

export default function ProductDetailPage() {
  const router = useRouter()
  const params = useParams()
  const { id } = params
  
  const [product, setProduct] = useState<any>(null)
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    const fetchProduct = async () => {
      try {
        const token = localStorage.getItem("token")
        const res = await fetch(`http://127.0.0.1:8000/api/products/${id}/`, {
          headers: { Authorization: `Token ${token}` }
        })
        if (res.ok) {
          setProduct(await res.json())
        } else {
          alert("Product not found")
          router.push("/products")
        }
      } catch (error) {
        console.error("Error fetching product:", error)
      } finally {
        setLoading(false)
      }
    }
    if (id) fetchProduct()
  }, [id, router])

  if (loading) return <div className="flex h-screen items-center justify-center">Loading...</div>
  if (!product) return null

  return (
    <div className="flex min-h-screen bg-background">
      <Sidebar />
      <main className="flex-1 ml-64 p-8">
        {/* Header */}
        <div className="flex items-center justify-between mb-8">
          <div className="flex items-center gap-4">
            <Button variant="ghost" size="icon" onClick={() => router.back()}>
              <ChevronLeft className="h-4 w-4" />
            </Button>
            <div>
              <h1 className="text-3xl font-bold tracking-tight">{product.name}</h1>
              <p className="text-muted-foreground">SKU: {product.sku}</p>
            </div>
          </div>
          <div className="flex gap-2">
            <Button variant="outline" className="text-red-600 hover:text-red-700">
              <Trash2 className="mr-2 h-4 w-4" /> Delete
            </Button>
            <Button>
              <Edit className="mr-2 h-4 w-4" /> Edit Product
            </Button>
          </div>
        </div>

        <div className="grid gap-6 md:grid-cols-2">
          {/* Main Info Card */}
          <Card>
            <CardHeader>
              <CardTitle>Product Information</CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="grid grid-cols-2 gap-4">
                <div>
                  <Label className="text-muted-foreground">Category</Label>
                  <p className="text-lg font-medium">{product.category_name || "None"}</p>
                </div>
                <div>
                  <Label className="text-muted-foreground">Unit of Measure</Label>
                  <p className="text-lg font-medium">{product.unit}</p>
                </div>
                <div>
                  <Label className="text-muted-foreground">Low Stock Threshold</Label>
                  <p className="text-lg font-medium">{product.low_stock_threshold}</p>
                </div>
              </div>
            </CardContent>
          </Card>

          {/* Stock Info Card */}
          <Card>
            <CardHeader>
              <CardTitle>Current Stock</CardTitle>
              <CardDescription>Real-time availability across warehouses</CardDescription>
            </CardHeader>
            <CardContent>
              <div className="flex items-center justify-center h-32 bg-muted/20 rounded-lg border border-dashed">
                <div className="text-center">
                  <p className="text-sm text-muted-foreground">Total Quantity On Hand</p>
                  {/* Note: You might need to fetch stock specifically if it's not in the product object */}
                  <h2 className="text-4xl font-bold text-primary">{product.quantity_on_hand || 0}</h2>
                  <p className="text-xs text-muted-foreground mt-1">{product.unit}</p>
                </div>
              </div>
            </CardContent>
          </Card>
        </div>
      </main>
    </div>
  )
}